"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import { api } from "@/lib/api";
import { Partido, PosicionTabla } from "@/types";
import { format } from "date-fns";
import { es } from "date-fns/locale";
import { toast } from "sonner";
import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";

// UI Components
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter, DialogTrigger } from "@/components/ui/dialog";
import { Label } from "@/components/ui/label";
import {
    Trophy, CalendarDays, PlusCircle, Save, ChevronLeft, ChevronRight, Filter,
    Trash2, AlertTriangle, FileDown, CalendarClock, Printer, ShieldAlert
} from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { cn } from "@/lib/utils";
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from "@/components/ui/alert-dialog";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";

// Definici贸n de grupo visual
interface EnfrentamientoGroup {
    id: string;
    local: { id: number; nombre: string; logo?: string };
    visita: { id: number; nombre: string; logo?: string };
    partidos: Partido[];
}

export default function TorneoDashboard() {
    const [activeTab, setActiveTab] = useState("fixture");

    // --- SELECTOR MAESTRO DE TORNEO ---
    const [torneoSeleccionado, setTorneoSeleccionado] = useState<string>("A");

    // Filtros secundarios
    const [serieTabla, setSerieTabla] = useState<string>("1era");

    const [allPartidos, setAllPartidos] = useState<Partido[]>([]);
    const [tabla, setTabla] = useState<PosicionTabla[]>([]);
    const [loading, setLoading] = useState(true);
    const [fechaActual, setFechaActual] = useState<number>(1);

    // Estados de Modales (Reprogramar, PDF, Seguridad)
    const [isReprogramarOpen, setIsReprogramarOpen] = useState(false);
    const [nuevaFechaReprogramacion, setNuevaFechaReprogramacion] = useState("");
    const [isPdfOpen, setIsPdfOpen] = useState(false);
    const [pdfAlcance, setPdfAlcance] = useState("fecha_actual");
    const [pdfSerie, setPdfSerie] = useState("todas");
    const [securityModalOpen, setSecurityModalOpen] = useState(false);
    const [partidoSeguridad, setPartidoSeguridad] = useState<Partido | null>(null);
    const [equipoCulpableId, setEquipoCulpableId] = useState<string>("");
    const [motivoSuspension, setMotivoSuspension] = useState("");

    // Helpers
    const getNombreSerie = (serie: string) => {
        if (!serie) return "S/N";
        const s = serie.toLowerCase().trim();
        if (s === 'super_senior' || s === '3era' || s.includes('super')) return 'Super Senior';
        if (s === '2da') return '2da Serie';
        if (s === '1era') return '1era Serie';
        return serie;
    };

    const getOrdenSerie = (serie: string) => {
        if (!serie) return 99;
        const s = serie.toLowerCase().trim();
        if (s === 'super_senior' || s === '3era' || s.includes('super')) return 1;
        if (s === '2da') return 2;
        if (s === '1era') return 3;
        return 99;
    };

    // Cargar Datos
    const fetchData = async () => {
        setLoading(true);
        try {
            const [partidosData, tablaData] = await Promise.all([
                api.getPartidos({ division: torneoSeleccionado }),
                api.getTablaPosiciones(serieTabla, torneoSeleccionado)
            ]);
            setAllPartidos(partidosData);
            setTabla(tablaData);
        } catch (error) {
            console.error(error);
            toast.error("Error al cargar datos");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchData();
        setFechaActual(1); // Resetear al cambiar de torneo
    }, [torneoSeleccionado, serieTabla]);

    // Acciones (UpdateScore, Eliminar, etc. - Mismo c贸digo que ten铆as antes)
    const handleUpdateScore = async (id: number) => {
        const inputLocal = document.getElementById(`local-${id}`) as HTMLInputElement;
        const inputVisita = document.getElementById(`visita-${id}`) as HTMLInputElement;
        if (!inputLocal || !inputVisita) return;
        try {
            await api.updateResultado(id, Number(inputLocal.value), Number(inputVisita.value));
            toast.success("Actualizado");
            fetchData();
        } catch (e) { toast.error("Error"); }
    };

    const handleEliminarFixture = async () => {
        try {
            await api.eliminarFixture(torneoSeleccionado);
            toast.success(`Torneo Serie ${torneoSeleccionado} eliminado`);
            setAllPartidos([]);
            setTabla([]);
            setFechaActual(1);
        } catch (e) { toast.error("Error al eliminar"); }
    };

    const handleReprogramar = async () => {
        if (!nuevaFechaReprogramacion) return toast.error("Falta fecha");
        try {
            await api.reprogramarFecha(fechaActual, nuevaFechaReprogramacion, torneoSeleccionado);
            toast.success("Reprogramado");
            setIsReprogramarOpen(false);
            fetchData();
        } catch (e) { toast.error("Error"); }
    };

    // L贸gica Visual
    const fechasDisponibles = Array.from(new Set(allPartidos.map(p => p.fecha_numero))).sort((a, b) => a - b);
    const partidosDeLaFecha = allPartidos.filter(p => p.fecha_numero === fechaActual);
    const enfrentamientos: EnfrentamientoGroup[] = [];
    partidosDeLaFecha.forEach((p) => {
        if (!p.local || !p.visita) return;
        const key = `${p.clubLocalId}-${p.clubVisitaId}`;
        let grupo = enfrentamientos.find(g => g.id === key);
        if (!grupo) {
            grupo = { id: key, local: p.local, visita: p.visita, partidos: [] };
            enfrentamientos.push(grupo);
        }
        grupo.partidos.push(p);
    });
    enfrentamientos.forEach(g => {
        g.partidos.sort((a, b) => getOrdenSerie(a.serie) - getOrdenSerie(b.serie));
    });

    return (
        <div className="container mx-auto py-8 max-w-6xl space-y-8">
            {/* HEADER */}
            <div className="flex flex-col md:flex-row justify-between items-center gap-4 bg-white p-6 rounded-xl border shadow-sm">
                <div>
                    <h1 className="text-2xl font-bold">Panel del Torneo</h1>
                    <div className="flex items-center gap-2 mt-2">
                        <span className="font-bold text-slate-500">Torneo:</span>
                        <Select value={torneoSeleccionado} onValueChange={setTorneoSeleccionado}>
                            <SelectTrigger className="w-[200px] font-bold text-blue-700 bg-blue-50 border-blue-200"><SelectValue /></SelectTrigger>
                            <SelectContent>
                                <SelectItem value="A"> Serie A (S谩bados)</SelectItem>
                                <SelectItem value="B"> Serie B (Domingos)</SelectItem>
                            </SelectContent>
                        </Select>
                    </div>
                </div>

                <div className="flex gap-2">
                    {allPartidos.length > 0 && (
                        <AlertDialog>
                            <AlertDialogTrigger asChild><Button variant="outline" className="text-red-600 border-red-200"><Trash2 className="mr-2 h-4 w-4" /> Eliminar</Button></AlertDialogTrigger>
                            <AlertDialogContent>
                                <AlertDialogHeader><AlertDialogTitle>驴Eliminar Torneo {torneoSeleccionado}?</AlertDialogTitle><AlertDialogDescription>Se borrar谩n todos los datos de esta serie.</AlertDialogDescription></AlertDialogHeader>
                                <AlertDialogFooter><AlertDialogCancel>Cancelar</AlertDialogCancel><AlertDialogAction onClick={handleEliminarFixture} className="bg-red-600">Eliminar</AlertDialogAction></AlertDialogFooter>
                            </AlertDialogContent>
                        </AlertDialog>
                    )}
                    <Link href="/torneo/generar"><Button className="bg-blue-600"><PlusCircle className="mr-2 h-4 w-4" /> Nuevo Torneo</Button></Link>
                </div>
            </div>

            <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
                <TabsList className="grid w-full max-w-md grid-cols-2 mb-6">
                    <TabsTrigger value="fixture">Fixture</TabsTrigger>
                    <TabsTrigger value="tabla">Tabla de Posiciones</TabsTrigger>
                </TabsList>

                <TabsContent value="fixture" className="space-y-6">
                    {/* ... (Aqu铆 va tu c贸digo de renderizado del Fixture que ya tienes) ... */}
                    {/* Por simplicidad, usa el mismo bloque de renderizado de tarjetas que te envi茅 antes */}
                    {fechasDisponibles.length > 0 ? (
                        <>
                            {/* Navegador Fechas */}
                            <div className="flex items-center justify-center gap-4 bg-white p-3 rounded-xl border mb-4">
                                <Button variant="ghost" onClick={() => setFechaActual(p => Math.max(p - 1, fechasDisponibles[0]))} disabled={fechaActual === fechasDisponibles[0]}><ChevronLeft /></Button>
                                <span className="font-bold text-lg">Fecha {fechaActual}</span>
                                <Button variant="ghost" onClick={() => setFechaActual(p => Math.min(p + 1, fechasDisponibles[fechasDisponibles.length - 1]))} disabled={fechaActual === fechasDisponibles[fechasDisponibles.length - 1]}><ChevronRight /></Button>
                            </div>
                            {/* Grid */}
                            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                                {enfrentamientos.map(cruce => (
                                    <Card key={cruce.id} className="overflow-hidden border shadow-sm">
                                        <div className="bg-slate-50 p-3 border-b flex justify-between font-bold text-sm">
                                            <span className="text-right w-1/2 pr-2">{cruce.local.nombre}</span>
                                            <span>VS</span>
                                            <span className="text-left w-1/2 pl-2">{cruce.visita.nombre}</span>
                                        </div>
                                        <CardContent className="p-0 divide-y">
                                            {cruce.partidos.map(p => (
                                                <div key={p.id} className="p-3 flex justify-between items-center text-sm hover:bg-slate-50">
                                                    <Badge variant="outline" className="w-24 justify-center">{getNombreSerie(p.serie)}</Badge>
                                                    <div className="flex gap-2 items-center">
                                                        <Input className="w-10 h-8 text-center p-0" defaultValue={p.goles_local} id={`local-${p.id}`} />
                                                        <span>-</span>
                                                        <Input className="w-10 h-8 text-center p-0" defaultValue={p.goles_visita} id={`visita-${p.id}`} />
                                                        <Button size="icon" variant="ghost" className="h-8 w-8" onClick={() => handleUpdateScore(p.id)}><Save className="w-4 h-4" /></Button>
                                                    </div>
                                                </div>
                                            ))}
                                        </CardContent>
                                    </Card>
                                ))}
                            </div>
                        </>
                    ) : (
                        <div className="text-center py-20 bg-slate-50 rounded border-dashed border">
                            <p>No hay torneo activo para la Serie {torneoSeleccionado}</p>
                        </div>
                    )}
                </TabsContent>

                <TabsContent value="tabla">
                    {/* ... (Aqu铆 va tu c贸digo de renderizado de Tabla) ... */}
                    <div className="flex justify-end mb-4">
                        <Select value={serieTabla} onValueChange={setSerieTabla}>
                            <SelectTrigger className="w-[180px]"><SelectValue /></SelectTrigger>
                            <SelectContent>
                                <SelectItem value="super_senior">Super Senior</SelectItem>
                                <SelectItem value="2da">2da Serie</SelectItem>
                                <SelectItem value="1era">1era Serie</SelectItem>
                            </SelectContent>
                        </Select>
                    </div>
                    <Card>
                        <CardContent className="p-0">
                            {/* Tabla simple */}
                            <table className="w-full text-sm text-left">
                                <thead className="bg-slate-50 font-bold text-xs uppercase"><tr><th className="p-3">Pos</th><th className="p-3">Club</th><th className="p-3">Pts</th><th className="p-3">PJ</th></tr></thead>
                                <tbody>
                                    {tabla.map((t, i) => (
                                        <tr key={i} className="border-b"><td className="p-3">{i + 1}</td><td className="p-3 font-medium">{t.club}</td><td className="p-3 font-bold">{t.pts}</td><td className="p-3">{t.pj}</td></tr>
                                    ))}
                                </tbody>
                            </table>
                        </CardContent>
                    </Card>
                </TabsContent>
            </Tabs>
        </div>
    );
}